.nav-wrap {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: rgba(255, 255, 255, 0.127);
    border-bottom: 0.1rem solid rgba(0, 0, 0);
    position: fixed;
    left: 0;
    right: 0;
    height: 3.5rem;
}

.nav-wrap-bg {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: white;
    /* border-bottom: 0.1rem solid rgba(0, 0, 0); */
    position: fixed;
    left: 0;
    right: 0;
    height: 3.5rem;
    z-index: 2;
}

.nav-wrap:hover {
    background-color: rgba(99, 99, 99, 0.4);
}

.nav-wrap-bg:hover {
    background-color: #476045e8;
}

.nav-wrap .scrolled {
    background-color: rgb(40, 49, 37);
}

.nav-logo {
    height: 2rem;
    margin-right: 0.25rem;
}

.nav-title:hover {
    background-clip: text;
    color: transparent;
    background-image: linear-gradient(to right, #ffe000, #1bda14);
}

.nav-text {
    width: auto;
    margin: 0;
}

.nav-title {
    color: black;
    margin: 0;
    padding: 1rem;
    align-items: center;
    display: flex;
}

.nav-elements {
    display: flex;
    padding-right: 2rem;
    align-items: center;
}

.nav-element {
    text-decoration: none;
    color: black;
    transition: 0.2s;
    padding: 0 0.5rem;
    margin: 0.75rem;
    font-weight: 600;
    letter-spacing: 0.2rem;
    font-size: 0.8rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

.nav-element:hover {
    color: rgb(255, 255, 255);
}

#orders-num {
    color: white;
    background-color: rgba(187, 10, 10, 0.545);
    border-radius: 30%;
    padding: 0.25rem;
    padding-left: 0.4rem;
    margin-left: 0.25rem;
    font-size: 0.6rem;
}

.nav-element-active {
    text-decoration: none;
    transition: 0.2s;
    padding: 0 0.5rem;
    margin: 0.75rem;
    font-weight: 600;
    letter-spacing: 0.2rem;
    font-size: 0.8rem;
    color: rgb(42, 165, 21);
    display: flex;
    align-items: center;
    justify-content: center;
}